<template>
<div class="section">
    <div class="container">
        <div class="header">
            <h3 class="title">Admin</h3>
          <router-link style="margin-left: 20px" class="edit" :to="{ name: 'Register' }">add new user</router-link>
            <div class="logout">
                <span @click="logout"><i class="fas fa-sign-out-alt"></i> Logout</span>
            </div>
        </div>
        <table class="table is-fullwidth" id="mytb">
            <thead>
                <tr>
                    <th>Name</th>
                    <th>Password</th>
                    <th>Email</th>
                    <th class="icon-group">Option</th>
                </tr>
            </thead>
            <tbody>
                <tr  v-for="user in users" :key="user.index">
                    <td :style=" user.fullnameEng ? `` : `color: blue` ">{{ user.fullnameEng ? user.fullnameEng : `new
                      user` }}</td>
                    <td>
                        <span class="pw">{{ user.genPassword }}</span>
                    </td>
                    <td>{{ user.email }}</td>
                    <td class="icon-group">
                          <span @click="toProfile(user._id)"><i class="fas fa-search"></i></span>
                        <span @click="generatePassword(user._id)"><i class="fas fa-redo-alt"></i></span>
                        <span @click="deleteUser(user._id)"><i class="fas fa-trash"></i></span>
                    </td>
                </tr>
            </tbody>
        </table>
    </div>
  <app-loading v-if="isLoading"></app-loading>
</div>
</template>

<script>
  import appLoading from '../components/Loading'
    export default {
    components: {
      appLoading
    },
        data: () => ({
          isLoading: false,
          users: []
        }),
      methods: {
         async fetchUsers () {
          try {
            const res = await this.$axios.get('user/get-all')
            this.users = res.data.users
          } catch (e) {
            throw new Error(e)
          }
          },
        toProfile (userId) {
           this.$router.push({ name: 'Profile', params: { id: userId } })
        },
       async generatePassword (userId) {
          try {
            this.$dialog.confirm('Please confirm to continue')
              .then(async () => {
                this.isLoading = true
                 await this.$axios.put('user/generate-password', {userId})
                 await this.fetchUsers()
                this.isLoading = false
              })
              .catch(function() {
                console.log('Clicked on cancel');
              });
          }catch (e) {
            this.isLoading = false
            throw new Error(e)
          }
        },
       async deleteUser (userId) {
          try {
            this.$dialog.confirm('Please confirm to continue')
              .then(async () => {
                this.isLoading = true
                await this.$axios.delete('user/delete-user', { params: { userId } })
                await this.fetchUsers()
                this.isLoading = false
              })
              .catch(function() {
                console.log('Clicked on cancel');
              });
          }catch (e) {
            this.isLoading = false
            throw new Error(e)
          }
        },
        async logout () {
          try {
            await this.$store.dispatch('logout')
            this.$notify({
              group: 'foo',
              title: 'ອອກຈາກລະບົບແລ້ວ',
            });
            this.$router.replace({ name: 'login' })
          } catch (e) {
            throw new Error(e)
          }
        }
      },
      created () {
          this.fetchUsers()
      }
    }
</script>

<style lang="scss" scoped>
.header{
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    h3{
        margin: 0;
        padding: 0;
    }
    .logout{
        cursor: pointer;
        padding: 5px 10px;
        background-color: $color-3;
        border-radius: 5px;
        margin-left: auto;
    }
}
.icon-group{
    font-size: 14px;
    width: 120px;
    span{
        cursor: pointer;
        padding: 5px;
        margin-left: 5px;
        &:hover{
            color: $color-1;
        }
    }
}
#mytb{
    border-collapse: separate;
    border-spacing: 0 10px;
    thead{
        th{
            border: none;
        }
    }
    tbody{
        tr{
            webkit-box-shadow: 0px 0px 0px 1px $color-3;
            -moz-box-shadow: 0px 0px 0px 1px $color-3;
            box-shadow: 0px 0px 0px 1px $color-3;
            td{
                border: none;
                vertical-align: middle;
                .pw{
                    color: $color-1;
                    cursor: pointer;
                }
            }
        }
    }
}
</style>
